import React from 'react'
import HomeStyled from './Nuestro.styled';
import imgHeader from '../../assets/images/img-servicios-header.png';
import imgProductosReparacion from '../../assets/images/img-servicios-reparacion.png';

import imgProductosPapel from '../../assets/images/img-servicios-papel.png';
import imgProductosMadera from '../../assets/images/img-servicios-madera.png';
import imgProductosMetal from '../../assets/images/img-servicios-metal.png';
import imgProductosPlast from '../../assets/images/img-servicios-plast.png';
import imgProductosCarton from '../../assets/images/img-servicios-carton.png';
import imgProductosScrap from '../../assets/images/img-servicios-scrap.png';

import { AnimationOnScroll } from 'react-animation-on-scroll';
import { Button, theme } from "antd";

export default function Home() {
  const { token: { colorPrimary } } = theme.useToken();


  return (
    <HomeStyled colorPrimary={colorPrimary} >

      <div className='seccion seccion-column nuestro'>
        <div className='seccion-row img-header'>
          <div className='title' >
            <AnimationOnScroll animateIn="animate__fadeInUp" >
              <h1>
                Nuestros
                <p> Servicios </p>
              </h1>
            </AnimationOnScroll>
          </div>
          <div className='image-container' >
            <img src={imgHeader} alt="produc-header" />
          </div>
        </div>
        <div className='seccion-row' >
          <div className='text'>
            <AnimationOnScroll animateIn="animate__fadeInUp" >
              <p>
                Las pallets de madera estandarizadas o hechas a medida, nuevas o reacondicionadas ofrecen una multitud de respuestas. Combinan parmetros econmicos y ecolgicos; nuestros pallets se producen a partir de un recurso renovable. Son fcilmente reparables y recuperables al final de su vida til. Ofrecen una imagen de naturalidad y autenticidad en perfecta armona con la sociedad actual, apegada a consideraciones medioambientales. La facilidad con la que se puede utilizar la madera permite producir todo tipo de pallets.
              </p>
            </AnimationOnScroll>
          </div>
        </div>
      </div>

      <div className='seccion seccion-column productos'>
        <div className='seccion-row img-header productos'>
          <div className='image-container' >
            <img src={imgProductosReparacion} alt="produc-header" />
          </div>
          <div className="sobre-side texto">
            <AnimationOnScroll animateIn="animate__fadeInUp" >
              <div className="parrafo">
                <h1>Servicios de reparacin de tarimas</h1>
                <p className='parrafo-text'>
                  Nuestro servicio de tarimas es 360, gracias a que contamos con reparacin de tarimas, con lo que podrs ahorrar hasta un 40% en costos de operacin y almacenaje.
                </p>
                <p className='parrafo-text' >
                  Beneficio empresa-medio ambiente con la reparacin de tus tarimas, para que no tengas la necesidad de comprar tarimas nuevas. Reciclamos el material que podamos rescatar de tus tarimas y hacemos la reparacin con nuestros materiales y te entregamos un producto renovado al 100%.
                </p>
                <p className='parrafo-text' >
                  Hacemos la recoleccin de tus tarimas daadashasta tu empresa y te los entregamos completamente reacondicionados, as evitando que compres pallets nuevos cada vez que se daen.
                </p>
              </div>
            </AnimationOnScroll>
          </div>
        </div>
      </div>

      <div className='seccion seccion-column recolectamos'>
        <div className='seccion-row img-header elegir recolectamos'>

          <div className="sobre-side texto">
            <AnimationOnScroll animateIn="animate__fadeInUp" >

              <div className="parrafo" >
                <h1>Recolectamos material no
                  <br />
                  txico como:
                </h1>
              </div>

              <div className="parrafo" >
                <p>
                  Tenemos la infraestructura adecuada para recolectar los desechos industriales no txicos que nuestros clientes requieran.
                  Somos una empresa socialmente responsable con el medio ambiente, por lo que buscamos que todos los desechos sean 100% reciclados y aprovechados de la mejor manera.
                </p>
              </div>

              <div className='pictures-container' >
                <div className="picture">
                  <img src={imgProductosMadera} alt="madera" />
                  <div className='picture-title' >Madera</div>
                </div>
                <div className="picture">
                  <img src={imgProductosPapel} alt="papel" />
                  <div className='picture-title black' >Papel</div>
                </div>
                <div className="picture">
                  <img src={imgProductosMetal} alt="metal" />
                  <div className='picture-title' >Metal</div>
                </div>
                <div className="picture">
                  <img src={imgProductosPlast} alt="plastico" />
                  <div className='picture-title black' >Plstico</div>
                </div>
                <div className="picture">
                  <img src={imgProductosCarton} alt="carton" />
                  <div className='picture-title' >Cartn</div>
                </div>

              </div>

            </AnimationOnScroll>
          </div>
        </div>
      </div>

      <div className='seccion seccion-column servicios'>
        <div className="servicios-container ">
          <div className='seccion-row img-header'>
            <div className="sobre-side texto">
              <AnimationOnScroll animateIn="animate__fadeInUp" >
                <div className="parrafo">
                  <h1>Servicios de <br /> recoleccin de SCRAP</h1>
                  <p className='parrafo-text'>
                    Contamos con el servicio integral de recoleccin de scrapt personalizado segn sean tus necesidades. Tenemos la infraestructura adecuada para recolectar los desechos industriales no txicos que nuestros clientes requieran.
                  </p>
                  <p className='parrafo-text'>
                    Somos una empresa socialmente responsable con el medio ambiente, por lo que buscamos que todos los desechos sean 100% reciclados y aprovechados de la mejor manera
                  </p>
                </div>
              </AnimationOnScroll>
            </div>
            <div className='image-container' >
              <img src={imgProductosScrap} alt="scrap" />
            </div>
          </div>
        </div>
      </div>

    </HomeStyled>
  )
}
